				loadSign:true,//
				commentArr:{},
				falg:false,//
				basic_url: Config.dev_url,
				value: '',
				options: [{
					value: '1',
					label: ''
				}, {
					value: '0',
					label: ''
				}],
				page: { //
					currentPage: 1,
					pageSize: 10,
					totalCount: 0
				},
				selectData: [], ///
				Select_STATUS:[],//--new
				modify:false,//
				edit: true, //
				noedit:false,
				selMenu:[],
				show: false,
				isok1: true,
				isok2: false,
				down: true,
				up: false,
				fullHeight: document.documentElement.clientHeight - 210+'px',
				dialogVisible: false, //
				dialogVisible3:false,//
				dialogVisible4:false,//
				activeNames: ['1', '2','3'], //
				labelPosition: 'right', //
				addtitle: true,
				modifytitle: false,
				viewtitle: false, //title
				noviews: true, //
				views: false,
				samplesForm:{
					ITEM_LINEList:[]
				},
				categoryList:[],
				userList:[],
				//Tree
				gridData: [], //
				isEditing: false,
				commentArr:{},//
				tips:'1',
				rules: { //
					PROXYNUM: [{ required: true, trigger: 'blur', message: ''}],//
					VENDOR: [{ required: true, trigger: 'blur', message: ''}],//
					V_NAME: [{ required: true, trigger: 'blur', message: ''}],//
					PRODUCT_COMPANY: [{ required: true, trigger: 'blur', message: ''}],//
					P_NAME: [{ required: true, trigger: 'blur', message: ''}],//
					DESCRIPTION: [{ required: true, trigger: 'blur', message: ''}],//
					PRODUCT_CODE: [
						{ required: true, trigger: 'blur', message: '',},
						{ trigger: 'blur', validator: this.Validators.isWorknumber},
					],//
					SN: [{ required: true, message: '', trigger: 'blur' }],
					TYPE: [{ required: true, trigger: 'blur', message: ''}],//
					QUATITY: [{ required: true, trigger: 'blur', validator: this.Validators.isInteger}],
					ACCEPTDATE: [{required: true, message: '', trigger: 'change' }],
					ACCEPT_DATE: [{required: true, message: '', trigger: 'change' }],
					RECIP_DATE: [{ required: true, message: '', trigger: 'change' }],
					STATUSDATE: [{required: true, message: '', trigger: 'change' }],
					MODEL: [{ required: false, trigger: 'blur', validator: this.Validators.isSpecificKey}],//
					OTHER: [{ required: false, trigger: 'blur', validator: this.Validators.isSpecificKey}],//
					MEMO: [{ required: false, trigger: 'blur', validator: this.Validators.isSpecificKey}],//
				},
			};
		},
		methods: {
			//
			rowClass({ row, rowIndex}) {
			    return 'text-align:center'
			},
			reset(){
            	this.samplesForm = {
					PROXYNUM: '',//
					ITEMNUM: '',//
					VENDOR: '',//
					PRODUCT_COMPANY: '',//
					V_NAME: '',//
					P_NAME: '',//
					DESCRIPTION: '',//
					PRODUCT_CODE: '',//
					MODEL: '',//
					QUATITY: '',//
					OTHER: '',//
					MEMO: '',//
					ACCEPTDATE: '',//
					ACCEPT_PERSON: '',//
					ACCEPT_DATE: '',//
					RECIP_PERSON: '',//
					RECIP_DATE: '',//
					STATE: '1',//
					STATEDesc: '',
					VERSION: '1',//
					STATUSDATE: '',//
					ENTERBY: '',//
					ENTERDATE: '',//
					CHANGEBY: '',//
					CHANGEDATE: '',//
					TYPE: '',//
					STATUS: '1',//
					ITEM_LINEList:[]
				}
            },
			//
			getProxy() {
				var url = this.basic_url + '/api-apps/app/inspectPro';
				this.$axios.get(url, {}).then((res) => {
					console.log(res.data);
					this.gridData= res.data.data;
				});
					this.dialogVisible = true;
			},
			dailogconfirm(type) { //
				if(this.selval.length == 0){
					this.$message({
						message:'',
						type:'warning'
					})
				}else if(this.selval.length == 1){
					this.samplesForm.PROXYNUM=this.selval[0].PROXYNUM;
					this.samplesForm.V_NAME=this.selval[0].V_NAME;
					this.samplesForm.VENDOR=this.selval[0].VENDOR;
					this.samplesForm.PROXYNUM=this.selval[0].PROXYNUM;
					this.samplesForm.P_NAME=this.selval[0].P_NAME;
					this.samplesForm.PRODUCT_COMPANY=this.selval[0].PRODUCT_UNIT;
					this.samplesForm.DESCRIPTION=this.selval[0].ITEM_NAME;
					this.samplesForm.PRODUCT_CODE=this.selval[0].ITEM_ID;//
					this.samplesForm.MODEL=this.selval[0].ITEM_MODEL;
					this.samplesForm.QUATITY=this.selval[0].ITEM_QUALITY;
					this.dialogVisible = false;
				}else if(this.selval.length > 1){
					this.$message({
						message:'',
						type:'warning'
					})
				}
			},

			SelChange(val) {
				this.selval = val;
			},
		
			//
			handleClose(done) {
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			
			visible() {//
				this.samplesForm.ACCEPT_DATE =  '2018-10-10';//
				this.$axios.get(this.basic_url + '/api-user/users/currentMap',{}).then((res)=>{
					this.samplesForm.DEPTID = res.data.deptId;
					this.samplesForm.ENTERBY = res.data.id;
					var date=new Date();
					this.samplesForm.ENTERDATE = this.$moment(date).format("YYYY-MM-DD  HH:mm:ss");
				}).catch((err)=>{
					this.$message({
						message:'',
						type:'error'
					})
				})
				this.reset();
				this.addtitle = true;
				this.modifytitle = false;
				this.viewtitle = false;
				this.noviews = true;
            	this.modify=false;
				this.show = true;
				this.views = false;
            	this.edit = true;
				this.noedit = false;
			},
			detail(dataid) { //
				this.$axios.get(this.basic_url + '/api-user/users/currentMap',{}).then((res)=>{
					this.samplesForm.DEPTID = res.data.deptId;//id
					this.samplesForm.CHANGEBY = res.data.id;
					var date=new Date();
					this.samplesForm.CHANGEDATE = this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
				}).catch((err)=>{
					this.$message({
						message:'',
						type:'error'
					})
				})
				this.$axios.get(this.basic_url + '/api-apps/app/item/' + dataid, {}).then((res) => {
					console.log(res.data);
					for(var i=0;i<res.data.ITEM_LINEList.length;i++){
						res.data.ITEM_LINEList[i].isEditing = false;
					}
					this.samplesForm = res.data;
					console.log(233333);
					console.log(this.samplesForm.STATE);
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
				this.viewtitle = false;
				this.addtitle = false;
				this.modifytitle = true;
				this.modify = true;
				this.views = false;
				this.noviews = true;
				this.show = true;
				this.edit = true;
				this.noedit = false;
			},
			//
			view(dataid) {
				this.addtitle = false;
				this.modifytitle = false;
				this.viewtitle = true;
				this.views = true; 
				this.noviews = false;
				this.edit = true;
				this.noedit = true;
				var url = this.basic_url + '/api-apps/app/item/' + dataid;
				this.$axios.get(url, {}).then((res) => {
					this.samplesForm = res.data;
					if(this.samplesForm.STATE == '1'){
						this.samplesForm.STATE == '';
					}else if(this.samplesForm.STATE == '2'){
						this.samplesForm.STATE == '';
					}else if(this.samplesForm.STATE == '3'){
						this.samplesForm.STATE == '';
					}else{
						this.samplesForm.STATE == '';
					}
					this.show = true;
				}).catch((err) => {
					this.$message({
						message: '',
						type: 'error'
					});
				});
			},
			iconOperation(row, column, cell, event){//Table-
				if(column.property ==="iconOperation"){
					row.isEditing = !row.isEditing
				}
			},
			sizeChange(val) {
				this.page.pageSize = val;
				this.requestData();
			},
			currentChange(val) {
				this.page.currentPage = val;
				this.requestData();
			},
			//  
			dateFormat(row, column) {
				var date = row[column.property];
				if(date == undefined) {
					return "";
				}
				return this.$moment(date).format("YYYY-MM-DD");
			},
			///
			getType() {
				var url = this.basic_url + '/api-user/dicts/findChildsByCode?code=type';
				this.$axios.get(url, {}).then((res) => {
					this.selectData = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			//
			getcategory(){
				this.dialogVisible3 = true;				
			},
			//
			getReceive(){
				this.tips = '1';
				this.$emit('request');
				this.dialogVisible4 = true;
			},
			//
			getCatch(){
				this.tips = '2';
				this.$emit('request');
				this.dialogVisible4 = true;
			},
			addPerson(){
				if(this.selval.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selval.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					if (this.tips == '1') {
						this.samplesForm.ACCEPT_PERSON = this.selval[0].username; //
						this.dialogVisible4 = false;
						this.$emit('request');
					}else if(this.tips == '2'){
						this.samplesForm.RECIP_PERSON = this.selval[0].username;//
						this.dialogVisible4 = false;
						this.$emit('request');
					}
				}
			},
			addproclass(){
				if(this.selval.length == 0){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else if(this.selval.length > 1){
					this.$message({
						message: '',
						type: 'warning'
					});
				}else{
					this.samplesForm.TYPE = this.selval[0].TYPE;
					this.dialogVisible3 = false;
					this.$emit('request');
				}
			},
			SelChange(val) {
				this.selval = val;
			},
			addfield() { 
				//Table
				console.log(this.samplesForm.QUATITY);
				this.samplesForm.ITEM_LINEList = [];
				var date=new Date();
				var time = this.$moment(date).format("YYYY-MM-DD");
				for(var i = 0;i<this.samplesForm.QUATITY;i++){
					var obj = {
						ITEMNUM:'',
						ITEM_STEP:i+1,
						SN:'',
						STATE:'1',
						STATEDesc:'',
						ENTERBY:'',
						ENTERDATE:time,
						CHANGEBY:'',
						isEditing: true
					};
					this.samplesForm.ITEM_LINEList.push(obj);
				}
			},

			// deleteRow(index, rows) {//Table-
			// 	rows.splice(index, 11);
			// },
			
			//
			close() {
				this.show = false;
				this.dialogVisible3 = false;
				this.dialogVisible4 = false;
				this.$emit('request');
			},
			// cancelForm() {
			// 	this.show = false;
			// 	this.reset();
			// },
			// reset() {
			// 	this.show = false;
			// },
			
			toggle(e) {
				if(this.isok1 == true) {
					this.maxDialog();
				} else {
					this.rebackDialog();
				}
			},
			maxDialog(e) { //
				this.isok1 = false;
				this.isok2 = true;
				$(".mask_div").width(document.body.clientWidth);
				$(".mask_div").height(document.body.clientHeight - 60);
				$(".mask_div").css("top", "60px");
			},
			//
			rebackDialog() { //
				this.isok1 = true;
				this.isok2 = false;
				$(".mask_div").css("width", "80%");
				$(".mask_div").css("height", "80%");
				$(".mask_div").css("top", "100px");
			},
			//
			save() {
				this.$refs.samplesForm.validate((valid) => {
					if (valid) {
						if(this.samplesForm.ITEM_LINEList.length<=0){
			        		this.$message({
								message: '',
								type: 'warning'
							});
							return false;
			        	}else{
							var url = this.basic_url + '/api-apps/app/item/saveOrUpdate';
							this.$axios.post(url, this.samplesForm).then((res) => {
								if(res.data.resp_code == 0) {
									this.$message({
										message: '',
										type: 'success'
									});
									//
									this.$emit('request');
								}
							}).catch((err) => {
								this.$message({
									message: '',
									type: 'error'
								});
							});
							this.falg = true;
						}
					} else {
						this.show = true;
						this.$message({
							message: '',
							type: 'warning'
						});
						this.falg = false;
					}
				});
			},
			//
			saveAndUpdate(){
				this.save();
				if(this.falg){
					this.show = false;
				}
				this.$emit('request');
			},
			//
			saveAndSubmit(){
				this.save();
				this.reset();
				this.$emit('request');
			},
			//
			generate(){
				console.log(this.samplesForm);
				var dataid = this.samplesForm.ID;
				var url =this.basic_url + '/api-apps/app/item/operate/isExcProxy?ID=' +dataid;
				this.$axios.get(url,{}).then((res) => {
					console.log(res);
						if(res.data.resp_code == 0) {
							this.$message({
								message:res.data.resp_msg,
								type: 'success'
							});
						var url =this.basic_url + '/api-apps/app/item/operate/createInspectProxy?ID='+dataid;
						this.$axios.get(url, {}).then((res) => {
							if(res.data.resp_code == 0) {
								this.$message({
									message:res.data.resp_msg,
									type: 'success'
								});
							}
						}).catch((err) => {
							this.$message({
								message: res.data.resp_msg,
								type: 'error'
							});
						});
						
						}else{
							this.$message({
								message: res.data.resp_msg,
								type: 'error'
							});
						}
					}).catch((err) => {
						this.$message({
							message: res.data.resp_msg,
							type: 'error'
						});
					});
			},
			loadMore () {
			   if (this.loadSign) {
			     this.loadSign = false
			     this.page.currentPage++
			     if (this.page.currentPage > Math.ceil(this.page.totalCount/this.page.pageSize)) {
			       return
			     }
			     setTimeout(() => {
			       this.loadSign = true
			     }, 1000)
			     this.requestData()
			   }
			 },
			requestData(index) {//
				var data = {
					page: this.page.currentPage,
					limit: this.page.pageSize,
				};
				this.$axios.get(this.basic_url + '/api-apps/app/productType', {
					params: data
				}).then((res) => {
					// console.log(res.data);
					this.page.totalCount = res.data.count;
					//
					let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize)
					if(this.page.currentPage >= totalPage) {
						this.loadSign = false
					} else {
						this.loadSign = true
					}
					this.commentArr[this.page.currentPage] = res.data.data
					let newarr = []
					for(var i = 1; i <= totalPage; i++) {

						if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {

							for(var j = 0; j < this.commentArr[i].length; j++) {
								newarr.push(this.commentArr[i][j])
							}
						}
					}
					this.categoryList = newarr;
				}).catch((wrong) => {})
				var url = this.basic_url + '/api-user/users';
				this.$axios.get(url, {
					params: data
				}).then((res) => {
					console.log(res);
					//this.userList = res.data.data;
					this.page.totalCount = res.data.count;
					//
					let totalPage = Math.ceil(this.page.totalCount / this.page.pageSize)
					if(this.page.currentPage >= totalPage) {
						this.loadSign = false
					} else {
						this.loadSign = true
					}
					this.commentArr[this.page.currentPage] = res.data.data
					let newarr = []
					for(var i = 1; i <= totalPage; i++) {

						if(typeof(this.commentArr[i]) != 'undefined' && this.commentArr[i].length > 0) {

							for(var j = 0; j < this.commentArr[i].length; j++) {
								newarr.push(this.commentArr[i][j])
							}
						}
					}
					this.userList = newarr;
				}).catch((wrong) => {})
			},
			getITEM_STATUS() {//
				var url =  this.basic_url + '/api-user/dicts/findChildsByCode?code=ITEM_STATUS';
				this.$axios.get(url, {}).then((res) => {
					console.log(res)
					// this.Select_STATUS = res.data;
				}).catch(error => {
					console.log('');
				})
			},
			handleClose(done) { //
				this.$confirm('')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			}
		},
		mounted() {
			this.getType();
			this.requestData();
			this.getITEM_STATUS();
		},
	}
</script>

<style>
	@import '../../assets/css/mask-modules.css';
	.table-funbc {
		position: absolute;
		top: 3px;
		right: 2px;
	}
</style>


