<template>
  <el-table
    :data="data"
    style="width: 100%;"
    :row-style="showTr" 
    border 
    stripe 
    :header-cell-style="rowClass" 
    :height="fullHeight" 
    @selection-change="SelChange"
    v-loading="loading"  
		element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(255, 255, 255, 0.9)">
    <el-table-column type="selection" width="55" fixed align="center">
		</el-table-column>
    <el-table-column v-for="(column, index) in columns" :key="column.dataIndex"
      :label="column.text" v-if="column.isShow && column.dataIndex != 'version'">
      <template slot-scope="scope">
        <span v-if="spaceIconShow(index)" v-for="(space, levelIndex) in scope.row._level" class="ms-tree-space"></span>
        <span class="button is-outlined is-primary is-small" v-if="toggleIconShow(index,scope.row)" @click="toggle(scope.$index)">
          <i v-if="!scope.row._expanded" class="el-icon-caret-right" aria-hidden="true"></i>
          <i v-if="scope.row._expanded" class="el-icon-caret-bottom" aria-hidden="true"></i>
        </span>
        <span v-else-if="index===0" class="ms-tree-space"></span>
        <span v-if="index===0"@click=view(scope.row)>{{scope.row[column.dataIndex]}}</span>
        <span v-else>{{scope.row[column.dataIndex]}}</span>          
      </template>
    </el-table-column>
    <el-table-column v-for="(column, index) in columns" :key="column.dataIndex"
      :label="column.text" v-if="column.isShow && column.dataIndex == 'version'" align="right">
      <template slot-scope="scope">
        <span v-if="spaceIconShow(index)" v-for="(space, levelIndex) in scope.row._level" class="ms-tree-space"></span>
        <span class="button is-outlined is-primary is-small" v-if="toggleIconShow(index,scope.row)" @click="toggle(scope.$index)">
          <i v-if="!scope.row._expanded" class="el-icon-caret-right" aria-hidden="true"></i>
          <i v-if="scope.row._expanded" class="el-icon-caret-bottom" aria-hidden="true"></i>
        </span>
        <span v-else-if="index===0" class="ms-tree-space"></span>
        <span v-if="index===0"@click=view(scope.row)>{{scope.row[column.dataIndex]}}</span>
        <span v-else>{{scope.row[column.dataIndex]}}</span>          
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
  import Utils from '../../assets/js/index.js'
  import deliver from '../../assets/js/deliver.js'
  export default {
    name: 'tree-grid',
    props: {
// 
      treeStructure: {
        type: Boolean,
        default: function () {
          return false
        }
      },
// 
      columns: {
        type: Array,
        default: function () {
          return []
        }
      },
      checkedName: {
        type: Array,
        default: function () {
          return []
        }
      },
      loading: {
        type: Boolean,
        default: function () {
          return []
        }
      }, 
// 
      dataSource: {
        type: Array,
        default: function () {
          return []
        }
      },
// url
      requestUrl: {
        type: String,
        default: function () {
          return ''
        }
      },
// 
      treeType: {
        type: String,
        default: function () {
          return 'normal'
        }
      },
// 
      defaultExpandAll: {
        type: Boolean,
        default: function () {
          return true;
        }
      }
    },
    data () {
      return {
        fullHeight: document.documentElement.clientHeight - 210+'px',//
      }
    },
    computed: {
    // 
      data: function () {
//    	for(let i=0;i<data.length;i++){
//							if(data[i].parentId == "-1" || data[i].parentId == "null") {
//							return data.isMenu = ""
//						} else {
//							return data.isMenu = ""
//						}
//					}
        let me = this
        if (me.treeStructure) {
          let data = Utils.MSDataTransfer.treeToArray(me.dataSource, null, null, me.defaultExpandAll)
          return data
        }
        return me.dataSource
      }
    },
    methods: {
      //
      rowClass({ row, rowIndex}) {
          // console.log(rowIndex) //0
          return 'text-align:center'
      },
    	//
    	SelChange(val) {
				this.selUser = val;
				//
				// childByValueon
        // this.childValue
				this.$emit('childByValue', this.selUser)
			},
    // 
       showTr: function (row, index) {
    let show = (row.row._parent ? (row.row._parent._expanded && row.row._parent._show) : true)
    row.row._show = show
    return show ? '' : 'display:none;'
   },

    // 
      toggle: function (trIndex) {
        let me = this
        let record = me.data[trIndex]
        record._expanded = !record._expanded
      },
      view: function (data) {
        console.log(data);
         this.$emit('childByValue', data)
        // deliver.$emit('data', this.view)
      },
    
    // 
      spaceIconShow (index) {
        let me = this
        if (me.treeStructure && index === 0) {
          return true
        }
        return false
      },
    // 
      toggleIconShow (index, record) {
        let me = this
        if (me.treeStructure && index === 0 && record.children && record.children.length > 0) {
          return true
        }
        return false
      },
    }
  }
</script>
<style scoped>
  .ms-tree-space{position: relative;
    top: 1px;
    display: inline-block;
    font-family: 'Glyphicons Halflings';
    font-style: normal;
    font-weight: 400;
    line-height: 1;
    width: 18px;
    height: 14px;}
  .ms-tree-space::before{content: ""}
  table td{
    line-height: 26px;
  }
</style>



